// src/utils/dateHelpers.js

/**
 * Thm s ngy vo mt ngy
 * @param {Date} date - Ngy gc
 * @param {number} days - S ngy cn thm
 * @returns {Date} Ngy mi
 */
export const addDays = (date, days) => {
  const result = new Date(date);
  result.setDate(result.getDate() + days);
  return result;
};

/**
 * Format datetime theo nh dng dd/mm | hh:mm
 * @param {Date|string|number} date - Ngy cn format
 * @returns {string} Chui  format hoc 'N/A'
 */
export const formatDateTime = (date) => {
  if (!date) return 'N/A';
  const d = date instanceof Date ? date : new Date(date);
  if (isNaN(d.getTime())) return 'N/A';
  
  const day = String(d.getDate()).padStart(2, '0');
  const month = String(d.getMonth() + 1).padStart(2, '0');
  const hours = String(d.getHours()).padStart(2, '0');
  const minutes = String(d.getMinutes()).padStart(2, '0');
  
  return `${day}/${month} | ${hours}:${minutes}`;
};

/**
 * Chuyn i gi tr thnh Date hp l
 * @param {Date|string|number} val - Gi tr cn chuyn
 * @returns {Date|null} Date object hoc null nu khng hp l
 */
export const toValidDate = (val) => {
  if (val instanceof Date && !isNaN(val.getTime())) return val;
  if (typeof val === 'string' || typeof val === 'number') {
    const d = new Date(val);
    return !isNaN(d.getTime()) ? d : null;
  }
  return null;
};

/**
 * Tnh time offset t start date
 * @param {Date} startDate - Ngy bt u
 * @param {Date} targetDate - Ngy mc tiu
 * @returns {number} Offset tnh bng milliseconds
 */
export const calculateTimeOffset = (startDate, targetDate) => {
  return targetDate.getTime() - startDate.getTime();
};

/**
 * Format ngy cho input type="date"
 * @param {Date} date - Ngy cn format
 * @returns {string} Chui yyyy-mm-dd
 */
export const formatDateForInput = (date) => {
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, '0');
  const day = String(date.getDate()).padStart(2, '0');
  return `${year}-${month}-${day}`;
};

/**
 * To tn file vi timestamp
 * @param {string} prefix - Tin t tn file
 * @param {string} extension - Phn m rng (.json, .pdf)
 * @returns {string} Tn file hon chnh
 */
export const generateFileName = (prefix, extension) => {
  const now = new Date();
  const pad = n => n.toString().padStart(2, '0');
  const dd = pad(now.getDate());
  const mm = pad(now.getMonth() + 1);
  const yy = now.getFullYear().toString().slice(-2);
  const hh = pad(now.getHours());
  const min = pad(now.getMinutes());
  const ss = pad(now.getSeconds());
  return `${prefix}_${dd}_${mm}_${yy}-${hh}_${min}_${ss}${extension}`;
};
