
import React, { useState, useEffect, useRef } from 'react';
import Toast from './components/common/Toast';
import './App.css';
import Header from './components/layout/Header';
import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';
import ControlPanel from './components/layout/ControlPanel';
import DetailPanel from './components/layout/DetailPanel'; // Import Panel mi
import BerthPlanner from './components/planner/BerthPlanner';
import { initialWaitingShips, initialBerthedShips } from './data/mockData';

const addDays = (date, days) => {
  const result = new Date(date);
  result.setDate(result.getDate() + days);
  return result;
};

function App() {
  // Modal xc nhn xa tu
  const [confirmRemove, setConfirmRemove] = useState({ show: false, ship: null });
  // Modal xc nhn xa tu khi waiting list
  const [confirmDeleteWaiting, setConfirmDeleteWaiting] = useState({ show: false, ship: null });
  // Password modal state
  const [showPasswordModal, setShowPasswordModal] = useState(true);
  const [passwordInput, setPasswordInput] = useState("");
  const [passwordError, setPasswordError] = useState("");
  const DEFAULT_PASSWORD = "HoangTT";
  const getCurrentPassword = () => {
    return localStorage.getItem("plannerPassword") || DEFAULT_PASSWORD;
  };
  const setCurrentPassword = (pw) => {
    localStorage.setItem("plannerPassword", pw);
  };
  // C  ngn useEffect lc tu khi ang khi phc k hoch
  const isRestoringPlan = useRef(false);
  // Hm m file k hoch v khi phc trng thi
  const handleOpenPlan = async () => {
    try {
      // To input file ng
      const input = document.createElement('input');
      input.type = 'file';
      input.accept = '.json,application/json';
      input.onchange = async (e) => {
        const file = e.target.files[0];
        if (!file) return;
        const text = await file.text();
        let data;
        try {
          data = JSON.parse(text);
        } catch (err) {
          setToast({ message: 'File khng hp l!', type: 'error' });
          return;
        }
        // Khi phc state
        if (Array.isArray(data.cranes)) {
          cranePositionsRef.current = data.cranes;
        }
        if (Array.isArray(data.berthedShips)) {
          isRestoringPlan.current = true;
          setBerthedShips(data.berthedShips.map(ship => ({
            ...ship,
            eta: ship.eta ? new Date(ship.eta) : null,
            etd: ship.etd ? new Date(ship.etd) : null
          })));
        }
        if (Array.isArray(data.waitingShips)) {
          setWaitingShips(data.waitingShips.map(ship => ({
            ...ship,
            eta: ship.eta ? new Date(ship.eta) : null,
            etd: ship.etd ? new Date(ship.etd) : null
          })));
        }
        if (data.startDate) {
          setStartDate(new Date(data.startDate));
        }
        if (data.numDays) {
          setNumDays(data.numDays);
        }
        setToast({ message: ' m v khi phc k hoch thnh cng!', type: 'success' });
      };
      input.click();
    } catch (err) {
      setToast({ message: 'Li khi m file k hoch!', type: 'error' });
    }
  };
  // Ref  ly v tr cu t BerthHeader
  const cranePositionsRef = useRef([]);
  // Thm tu mi vo waiting list
  const handleAddWaitingShip = (ship) => {
    setWaitingShips(prev => [...prev, ship]);
    setToast({ message: ` thm tu ${ship.name} vo danh sch ch.`, type: 'success' });
  };

  // Xa tu khi waiting list
  const handleDeleteWaitingShip = (ship) => {
    if (!confirmDeleteWaiting.show) {
      setConfirmDeleteWaiting({ show: true, ship });
    }
  };

  const confirmDeleteWaitingShip = () => {
    if (confirmDeleteWaiting.ship) {
      setWaitingShips(prev => prev.filter(s => s.id !== confirmDeleteWaiting.ship.id));
      setToast({ message: ` xa tu ${confirmDeleteWaiting.ship.name} khi danh sch ch.`, type: 'info' });
    }
    setConfirmDeleteWaiting({ show: false, ship: null });
  };

  const cancelDeleteWaitingShip = () => {
    setConfirmDeleteWaiting({ show: false, ship: null });
  };

  // Cp tu t waiting list vo planner
  const handleDockShipFromWaiting = (ship) => {
    // Kim tra tu c y  thng tin khng
    if (!ship.eta || !ship.etd || !ship.berthName || !ship.loa) {
      setToast({ message: `Tu ${ship.name} cha  thng tin (ETA, ETD, Berth, LOA). Vui lng cp nht trc khi cp cu.`, type: 'error' });
      return;
    }
    
    const berthRef = {
      'K12C': 10,
      'K12A': 229,
      'K12': 361,
      'K12B': 549,
      'TT2': 773,
    };
    const refStart = berthRef[ship.berthName] || 10;
    
    // Tnh startPosition v endPosition
    // u tin s dng start/end t ship nu c, nu khng th dng startPosition/endPosition, cui cng mi dng gi tr mc nh
    let startPosition, endPosition;
    if (ship.start !== undefined && ship.start !== null && !isNaN(Number(ship.start))) {
      startPosition = Number(ship.start);
      endPosition = ship.end !== undefined && ship.end !== null && !isNaN(Number(ship.end)) 
        ? Number(ship.end) 
        : startPosition + (ship.loa || 100);
    } else if (ship.startPosition !== undefined && ship.startPosition !== null) {
      startPosition = ship.startPosition;
      endPosition = ship.endPosition || (startPosition + (ship.loa || 100));
    } else {
      startPosition = 0; // Mc nh l v tr u bn
      endPosition = ship.loa || 100; // Mc nh l LOA ca tu
    }
    
    // Tnh left v width da trn startPosition v endPosition, tr local zero nu c
    const FIRST_POS_BY_BERTH = {
      K12C: 0,
      K12A: 0,
      K12: 132,
      K12B: 320,
      TT2: 0,
    };
    const localZero = FIRST_POS_BY_BERTH[ship.berthName] || 0;
    const absStart = refStart + (startPosition - localZero);
    const absEnd = refStart + (endPosition - localZero);
    const shipWidth = absEnd - absStart;
    const left = `calc(${absStart}/1005*100%)`;
    const width = `calc(${shipWidth}/1005*100%)`;
    
    // Kim tra chng ln trc khi thm vo planner
    const slotHeight = 30;
    const msPerSlot = 12 * 60 * 60 * 1000;
    const startDateMs = startDate.getTime();
    const eta = ship.eta instanceof Date ? ship.eta : new Date(ship.eta);
    const etd = ship.etd instanceof Date ? ship.etd : new Date(ship.etd);
    const topPx = ((eta.getTime() - startDateMs) / msPerSlot) * slotHeight;
    const heightPx = ((etd.getTime() - eta.getTime()) / msPerSlot) * slotHeight;
    const top = `${topPx}px`;
    const height = `${heightPx}px`;
    
    const { overlap, gapWarning, overlapShip } = checkOverlapAndGap(
      { ...ship, loa: ship.loa, berthName: ship.berthName },
      berthedShips,
      left,
      width,
      top,
      height,
      startDateMs
    );
    
    if (overlap) {
      setToast({ message: `Li: Tu ${ship.name} b chng ln vi tu ${overlapShip?.name || ''} ti bn ${ship.berthName}. Vui lng kim tra li thi gian v v tr!`, type: 'error' });
      return;
    }
    
    const newShip = {
      ...ship,
      eta,
      etd,
      startPosition,
      endPosition,
      style: {
        ...ship.style,
        left,
        width,
        top,
        height,
      },
      gapWarning: gapWarning
    };
    
    setBerthedShips(prev => [...prev, newShip]);
    setWaitingShips(prev => prev.filter(s => s.id !== ship.id));
    setToast({ message: gapWarning
      ? `Cnh bo: Tu ${ship.name} c cp cu nhng khong cch vi tu khc nh hn 10% LOA tu ln hn!`
      : `Tu ${ship.name}  cp cu thnh cng.`,
      type: gapWarning ? 'warning' : 'success' });
  };

  const [toast, setToast] = useState({ message: '', type: 'info' });
  // Xa tu khi planner (RI)
  const handleRemoveShip = (ship) => {
    if (!confirmRemove.show) {
      setConfirmRemove({ show: true, ship });
    }
  };

  const confirmRemoveShip = () => {
    if (confirmRemove.ship) {
      setBerthedShips(prev => prev.filter(s => s.id !== confirmRemove.ship.id));
      setSelectedShip(null);
      setToast({ message: `Tu ${confirmRemove.ship.name}  ri cu.`, type: 'success' });
    }
    setConfirmRemove({ show: false, ship: null });
  };

  const cancelRemoveShip = () => {
    setConfirmRemove({ show: false, ship: null });
  };

  // Chuyn tu v waiting list (CH)
  const handleMoveToWaiting = (ship) => {
    setBerthedShips(prev => prev.filter(s => s.id !== ship.id));
    // Preserve all fields, only remove planner-specific style fields
    const cleanedShip = {
      ...ship,
      style: ship.style ? { ...ship.style, left: undefined, top: undefined } : undefined
    };
    setWaitingShips(prev => [...prev, cleanedShip]);
    setSelectedShip(null);
    setToast({ message: `Tu ${ship.name}  chuyn v danh sch ch.`, type: 'info' });
  };
  
  // Khi phc d liu t localStorage nu c
  const loadFromLocalStorage = () => {
    try {
      const saved = localStorage.getItem('berthPlannerState');
      if (saved) {
        const data = JSON.parse(saved);
        return {
          berthedShips: (data.berthedShips || []).map(ship => ({
            ...ship,
            eta: ship.eta ? new Date(ship.eta) : null,
            etd: ship.etd ? new Date(ship.etd) : null
          })),
          waitingShips: (data.waitingShips || []).map(ship => ({
            ...ship,
            eta: ship.eta ? new Date(ship.eta) : null,
            etd: ship.etd ? new Date(ship.etd) : null
          })),
          startDate: data.startDate ? new Date(data.startDate) : null,
          numDays: data.numDays || 7,
          cranes: data.cranes || []
        };
      }
    } catch (err) {
      console.error('Li khi c localStorage:', err);
    }
    return null;
  };

  const savedState = loadFromLocalStorage();
  
  // Khi phc crane positions t localStorage
  useEffect(() => {
    if (savedState?.cranes) {
      cranePositionsRef.current = savedState.cranes;
    }
  }, []); // Ch chy 1 ln khi mount
  
  const [waitingShips, setWaitingShips] = useState(savedState?.waitingShips || []); 
  const [berthedShips, setBerthedShips] = useState(savedState?.berthedShips || []); 
  const [selectedShip, setSelectedShip] = useState(null); // null = khng c tu no c chn
  const [activeBerth, setActiveBerth] = useState(null); // Berth ang c highlight khi drag
  const [numDays, setNumDays] = useState(savedState?.numDays || 7);
  const [startDate, setStartDate] = useState(() => {
    if (savedState?.startDate) return savedState.startDate;
    const now = new Date();
    now.setHours(0, 0, 0, 0);
    return now;
  });

  // Lu state vo localStorage mi khi thay i
  useEffect(() => {
    try {
      const dataToSave = {
        berthedShips,
        waitingShips,
        startDate,
        numDays,
        cranes: cranePositionsRef.current || []
      };
      localStorage.setItem('berthPlannerState', JSON.stringify(dataToSave));
    } catch (err) {
      console.error('Li khi lu localStorage:', err);
    }
  }, [berthedShips, waitingShips, startDate, numDays]);

  // useEffect  chuyn tu ra khi planner v waiting list nu khng cn trong khung ngy
  useEffect(() => {
    // Dng mt c  trnh chy logic ny khi component mi mount ln u
    // v state cha n nh hon ton.
    const timer = setTimeout(() => {
      const newEndDate = addDays(startDate, parseInt(numDays, 10));
      
      const shipsToMove = [];
      const shipsToKeep = [];

      berthedShips.forEach(ship => {
        const shipEta = ship.eta ? new Date(ship.eta) : null;
        const shipEtd = ship.etd ? new Date(ship.etd) : null;

        // Tu nm hon ton ngoi khung ngy (kt thc trc khi bt u, hoc bt u sau khi kt thc)
        if (!shipEta || !shipEtd || shipEtd <= startDate || shipEta >= newEndDate) {
          shipsToMove.push({
            ...ship,
            eta: null,
            etd: null,
            berthName: null,
            mandra: null,
            style: {}
          });
        } else {
          shipsToKeep.push(ship);
        }
      });

      // Nu c tu cn di chuyn, cp nht state
      if (shipsToMove.length > 0) {
        setBerthedShips(shipsToKeep);
        setWaitingShips(prevWaiting => [...prevWaiting, ...shipsToMove]);
        setToast({ message: `${shipsToMove.length} tu  c chuyn v danh sch ch.`, type: 'info' });
      }
    }, 100); // Thm mt khong tr nh  m bo state  c cp nht

    return () => clearTimeout(timer);
  }, [startDate, numDays]);

  // a logic lc vo hm ring - B HM NY v khng cn cn thit
  // Khng cn cn lc t initialBerthedShips/initialWaitingShips
  // V d liu  c lu trong state v localStorage

  // Hm ny gi khi select thay i
  const handleDayChange = (newDayCount) => {
    setNumDays(newDayCount); // Cp nht state (s t ng lu vo localStorage)
  };

  const handleStartDateChange = (newDate) => {
    setStartDate(newDate); // Cp nht state (s t ng lu vo localStorage)
  };

  const handleSelectShip = (ship) => {
    setSelectedShip(ship);
  };

  const handleClosePanel = () => {
    setSelectedShip(null);
  };

  const handleShipPositionChange = (shipId, newPosition) => {
    // Cp nht activeBerth khi ang ko
    if (newPosition.berthName) {
      setActiveBerth(newPosition.berthName);
    }
    
    setBerthedShips(prevShips => 
      prevShips.map(ship => {
        if (ship.id === shipId) {
          const updatedShip = {
            ...ship,
            style: {
              ...ship.style,
              left: newPosition.left,
            }
          };
          
          // Cp nht berthName nu c thay i
          if (newPosition.berthName) {
            updatedShip.berthName = newPosition.berthName;
          }
          
          // Tnh ton startPosition v endPosition da trn left v LOA
          if (newPosition.left && ship.loa) {
            const berthRef = {
              'K12C': 10,
              'K12A': 229,
              'K12': 361,
              'K12B': 549,
              'TT2': 773,
            };
            const leftMatch = newPosition.left.match(/calc\((-?\d+)\s*\/\s*(\d+)\s*\*\s*100%\)/);
            if (leftMatch) {
              const absStart = parseInt(leftMatch[1]);
              const refStart = berthRef[updatedShip.berthName] || 10;
              const FIRST_POS_BY_BERTH = {
                K12C: 0,
                K12A: 0,
                K12: 132,
                K12B: 320,
                TT2: 0,
              };
              const localZero = FIRST_POS_BY_BERTH[updatedShip.berthName] || 0;
              updatedShip.startPosition = absStart - refStart + localZero;
              updatedShip.endPosition = updatedShip.startPosition + ship.loa;
            }
          }
          
          // Nu rollback vi eta/etd gc, khi phc trc tip
          if (newPosition.rollbackEta && newPosition.rollbackEtd) {
            updatedShip.eta = newPosition.rollbackEta;
            updatedShip.etd = newPosition.rollbackEtd;
          }
          // Nu c thay i thi gian, cp nht ETA v ETD
          else if (newPosition.timeOffset !== undefined) {
            const newEta = new Date(startDate.getTime() + newPosition.timeOffset);
            let duration;
            const etaValid = ship.eta instanceof Date && !isNaN(ship.eta.getTime());
            const etdValid = ship.etd instanceof Date && !isNaN(ship.etd.getTime());
            if (etaValid && etdValid) {
              duration = ship.etd.getTime() - ship.eta.getTime();
            } else {
              duration = 24 * 60 * 60 * 1000; // 1 ngy
            }
            const newEtd = new Date(newEta.getTime() + duration);
            updatedShip.eta = newEta;
            updatedShip.etd = newEtd;
          }
          
          return updatedShip;
        }
        return ship;
      })
    );
    // selectedShip s t ng c cp nht qua useEffect
  };

  const handleShipDragEnd = () => {
    // Reset activeBerth khi th chut
    setActiveBerth(null);
  };

  // Ly selectedShip mi nht t berthedShips
  const currentSelectedShip = selectedShip 
    ? berthedShips.find(s => s.id === selectedShip.id) || selectedShip
    : null;

  // Click bt k u ngoi planner  deselect
  const handleAppClick = (event) => {
    // Ch deselect nu click vo app container, khng phi cc phn t con
    if (event.target.classList.contains('app') || 
        event.target.classList.contains('main-content')) {
      setSelectedShip(null);
    }
  };

  // X l drop tu t waiting list vo grid
  useEffect(() => {
  window.onShipDropFromWaiting = (ship, event) => {
      // Xc nh v tr th da vo ta  chut
      // 1. Xc nh ct (berth) theo v tr X
      // 2. Xc nh slot thi gian (ETA/ETD) theo v tr Y
      // Ly bounding rect ca grid-main
      const grid = document.querySelector('.grid-main');
      if (!grid) return;
      const rect = grid.getBoundingClientRect();
      const x = event.clientX - rect.left;
      const y = event.clientY - rect.top;

      // Tnh ton ct (berth)
      // Ly width tng ct (da vo s ct v gap)
      // Cu trc: gap10 | K12C | gap30 | K12A | K12 | K12B | gap20 | TT2 | gap10
      // Tnh ton v tr cc block theo px
      const totalWidth = rect.width;
      // T l cc block (da vo mt):
      // gap10: 10m, K12C: 189m, gap30: 30m, K12A: 132m, K12: 188m, K12B: 204m, gap20: 20m, TT2: 222m, gap10: 10m
      const blockDefs = [
        { id: 'gap', meters: 10 },
        { id: 'K12C', meters: 189 },
        { id: 'gap', meters: 30 },
        { id: 'K12A', meters: 132 },
        { id: 'K12', meters: 188 },
        { id: 'K12B', meters: 204 },
        { id: 'gap', meters: 20 },
        { id: 'TT2', meters: 222 },
        { id: 'gap', meters: 10 },
      ];
      let px = 0;
      let berthName = null;
      for (let i = 0; i < blockDefs.length; i++) {
        const blockPx = blockDefs[i].meters / 1005 * totalWidth;
        if (x >= px && x < px + blockPx) {
          if (blockDefs[i].id !== 'gap') berthName = blockDefs[i].id;
          break;
        }
        px += blockPx;
      }
      if (!berthName) return; // Khng th vo vng hp l

      // Tnh ton ETA/ETD theo v tr Y
      // Mi SLOT_HEIGHT = 12h
      const slot = Math.floor(y / 30);
      const eta = new Date(startDate.getTime() + slot * 12 * 60 * 60 * 1000);
      // Gi nguyn duration tu (nu c), nu khng c ETD th mc nh ETD = ETA + 1 ngy
      let duration;
      const etaValid = ship.eta instanceof Date && !isNaN(ship.eta.getTime());
      const etdValid = ship.etd instanceof Date && !isNaN(ship.etd.getTime());
      if (etaValid && etdValid) {
        duration = ship.etd.getTime() - ship.eta.getTime();
      } else {
        duration = 24 * 60 * 60 * 1000; // 1 ngy
      }
      const etd = new Date(eta.getTime() + duration);

      // Tnh left style (t l mt trn tng 1005m)
      // Ly refStart ca berth
      const berthRef = {
        'K12C': 10,
        'K12A': 229,
        'K12': 361,
        'K12B': 549,
        'TT2': 773,
      };
      const refStart = berthRef[berthName] || 10;
      
      // m bo eta/etd l kiu Date
      const toValidDate = val => {
        if (val instanceof Date && !isNaN(val.getTime())) return val;
        if (typeof val === 'string' || typeof val === 'number') {
          const d = new Date(val);
          return !isNaN(d.getTime()) ? d : null;
        }
        return null;
      };
      const etaDate = toValidDate(ship.eta);
      const etdDate = toValidDate(ship.etd);
      
      // Tnh startPosition v endPosition
      // u tin s dng start/end t ship nu c, nu khng th dng startPosition/endPosition, cui cng mi dng gi tr mc nh
      let startPosition, endPosition;
      if (ship.start !== undefined && ship.start !== null && !isNaN(Number(ship.start))) {
        startPosition = Number(ship.start);
        endPosition = ship.end !== undefined && ship.end !== null && !isNaN(Number(ship.end)) 
          ? Number(ship.end) 
          : startPosition + (ship.loa || 100);
      } else if (ship.startPosition !== undefined && ship.startPosition !== null) {
        startPosition = ship.startPosition;
        endPosition = ship.endPosition || (startPosition + (ship.loa || 100));
      } else {
        startPosition = 0; // Mc nh l v tr u bn
        endPosition = ship.loa || 100; // Mc nh l LOA ca tu
      }
      
      // Tnh li left da trn startPosition
      const absStart = refStart + startPosition;
      const left = `calc(${absStart}/1005*100%)`;
      
      // Tnh width da trn khong cch start-end
      const shipWidth = endPosition - startPosition;
      const width = `calc(${shipWidth}/1005*100%)`;
      
      const newShip = {
        ...ship,
        berthName,
        eta: etaDate,
        etd: etdDate,
        startPosition,
        endPosition,
        style: {
          ...ship.style,
          left,
          width,
        },
      };
      setBerthedShips(prev => [...prev, newShip]);
      setWaitingShips(prev => prev.filter(s => s.id !== ship.id));
      setSelectedShip(newShip); // T ng chn tu va th
      setToast({ message: `Tu ${ship.name}  cp cu thnh cng.`, type: 'success' });
    };
    return () => { window.onShipDropFromWaiting = null; };
  }, [startDate]);

  // Handler cp nht k hoch tu t DetailPanel
  // Kim tra chng ln v khong cch 10% LOA
  const checkOverlapAndGap = (ship, allShips, left, width, top, height, startDateMs) => {
    // left: calc(x/1005*100%), width: calc(w/1005*100%), top: y px, height: h px
    const leftMatch = left && left.match(/calc\((-?\d+)\s*\/\s*(\d+)\s*\*\s*100%\)/);
    const widthMatch = width && width.match(/calc\((\d+)\s*\/\s*(\d+)\s*\*\s*100%\)/);
    if (!leftMatch || !widthMatch) return { overlap: false, gapWarning: false, overlapShip: null };
    const newShipStart = parseInt(leftMatch[1]);
    const shipWidthMeters = parseInt(widthMatch[1]);
    const newShipEnd = newShipStart + shipWidthMeters;
    const newShipTop = top ? parseFloat(top) : 0;
    const newShipBottom = newShipTop + (height ? parseFloat(height) : 0);
    let gapWarning = false;
    
    // Hm tnh style cho tu nu cha c
    const calculateShipStyle = (otherShip) => {
      if (!otherShip.eta || !otherShip.etd) return null;
      const slotHeight = 30;
      const msPerSlot = 12 * 60 * 60 * 1000;
      const oTop = ((otherShip.eta.getTime() - startDateMs) / msPerSlot) * slotHeight;
      const oHeight = ((otherShip.etd.getTime() - otherShip.eta.getTime()) / msPerSlot) * slotHeight;
      return { top: oTop, height: oHeight };
    };
    
    for (let otherShip of allShips) {
      if (otherShip.id === ship.id) continue;
      
      // Kim tra cc tu c th chng ln: cng berth hoc cc berth lin k (K12A, K12, K12B dng chung h quy chiu)
      const berthGroups = {
        'K12C': ['K12C'],
        'K12A': ['K12A', 'K12', 'K12B'],
        'K12': ['K12A', 'K12', 'K12B'],
        'K12B': ['K12A', 'K12', 'K12B'],
        'TT2': ['TT2']
      };
      const shipGroup = berthGroups[ship.berthName] || [];
      const otherGroup = berthGroups[otherShip.berthName] || [];
      const canOverlap = shipGroup.some(b => otherGroup.includes(b));
      
      if (!canOverlap) continue;
      
      // So snh v tr
      const oLeftMatch = otherShip.style?.left && otherShip.style.left.match(/calc\((-?\d+)\s*\/\s*(\d+)\s*\*\s*100%\)/);
      const oWidthMatch = otherShip.style?.width && otherShip.style.width.match(/calc\((\d+)\s*\/\s*(\d+)\s*\*\s*100%\)/);
      if (!oLeftMatch || !oWidthMatch) continue;
      const otherStart = parseInt(oLeftMatch[1]);
      const otherWidth = parseInt(oWidthMatch[1]);
      const otherEnd = otherStart + otherWidth;
      
      // Tnh hoc ly top/height ca tu khc
      let oTop, oHeight, otherBottom;
      if (otherShip.style?.top && otherShip.style?.height) {
        oTop = parseFloat(otherShip.style.top);
        oHeight = parseFloat(otherShip.style.height);
        otherBottom = oTop + oHeight;
      } else {
        const calculated = calculateShipStyle(otherShip);
        if (!calculated) continue;
        oTop = calculated.top;
        oHeight = calculated.height;
        otherBottom = oTop + oHeight;
      }
      
      // Kim tra overlap theo 2 chiu
      const horizontalOverlap = !(newShipEnd <= otherStart || newShipStart >= otherEnd);
      const verticalOverlap = !(newShipBottom <= oTop || newShipTop >= otherBottom);
      if (horizontalOverlap && verticalOverlap) {
        return { overlap: true, gapWarning: false, overlapShip: otherShip };
      }
      // Cnh bo ch khi thc s giao nhau trn trc thi gian (overlap dc)
      const verticalOverlapOnly = (newShipTop < otherBottom && newShipBottom > oTop);
      const horizontalNoOverlap = (newShipEnd <= otherStart || newShipStart >= otherEnd);
      if (verticalOverlapOnly && horizontalNoOverlap) {
        // Khong cch gia 2 tu (mp phi tu tri v mp tri tu phi)
        const dist = Math.min(Math.abs(newShipStart - otherEnd), Math.abs(otherStart - newShipEnd));
        // Ly LOA ln hn
        const loa1 = Number(ship.loa) || 0;
        const loa2 = Number(otherShip.loa) || 0;
        const minGap = 0.1 * Math.max(loa1, loa2);
        if (dist < minGap) {
          gapWarning = true;
        }
      }
    }
    return { overlap: false, gapWarning, overlapShip: null };
  };

  const handleUpdateShipPlan = (updatedShip) => {
    // Tnh li style.left, style.width, style.top, style.height da trn berthName, loa, eta, etd
    const berthRef = {
      'K12C': 10,
      'K12A': 229,
      'K12': 361,
      'K12B': 549,
      'TT2': 773,
    };
    const totalMeters = 1005;
    const slotHeight = 30;
    const msPerSlot = 12 * 60 * 60 * 1000;
    const startDateMs = startDate.getTime();
    const eta = updatedShip.eta ? new Date(updatedShip.eta) : null;
    const etd = updatedShip.etd ? new Date(updatedShip.etd) : null;
    const berthName = updatedShip.berthName;
    const loa = Number(updatedShip.loa);

    // Nu tu ang  waitingShips th cp nht vo waitingShips
    const isWaiting = waitingShips.some(s => s.id === updatedShip.id);
    if (isWaiting) {
      // Tnh startPosition v endPosition t start/end nu c
      let startPosition, endPosition;
      if (updatedShip.start !== undefined && updatedShip.end !== undefined) {
        startPosition = Number(updatedShip.start);
        endPosition = Number(updatedShip.end);
      }
      
      setWaitingShips(prev => prev.map(s => s.id === updatedShip.id ? {
        ...s,
        ...updatedShip,
        eta,
        etd,
        startPosition,
        endPosition
      } : s));
      setToast({ message: ` cp nht thng tin tu ${updatedShip.name} trong danh sch ch.`, type: 'success' });
      setSelectedShip(null);
      return;
    }

    // Tm tu hin ti trong berthedShips  ly gi tr c
    const currentShip = berthedShips.find(s => s.id === updatedShip.id);

    let left = currentShip?.style?.left, width = currentShip?.style?.width, top = undefined, height = undefined;
    // Nu c start/end trong updatedShip, tnh li left/width theo start/end/LOA
    if (berthName && loa && eta && etd) {
      let useStart = updatedShip.start !== undefined ? Number(updatedShip.start) : undefined;
      let useEnd = updatedShip.end !== undefined ? Number(updatedShip.end) : undefined;
      if (!isNaN(useStart) && !isNaN(useEnd)) {
        const refStart = berthRef[berthName] || 10;
        const absStart = refStart + useStart;
        const absEnd = refStart + useEnd;
        const newWidth = absEnd - absStart;
        left = `calc(${absStart}/1005*100%)`;
        width = `calc(${newWidth}/1005*100%)`;
      } else if (!currentShip || currentShip.berthName !== berthName || currentShip.loa !== loa) {
        const refStart = berthRef[berthName] || 10;
        left = `calc(${refStart}/1005*100%)`;
        width = `calc(${loa}/1005*100%)`;
      }
      // Lun tnh li top, height da trn eta/etd
      const topPx = ((eta.getTime() - startDateMs) / msPerSlot) * slotHeight;
      const heightPx = ((etd.getTime() - eta.getTime()) / msPerSlot) * slotHeight;
      top = `${topPx}px`;
      height = `${heightPx}px`;
    }
    // Kim tra chng ln v 10% LOA, loi b chnh tu ang cp nht khi danh sch so snh
    setBerthedShips(prev => {
      const otherShips = prev.filter(s => s.id !== updatedShip.id);
      const { overlap, gapWarning, overlapShip } = checkOverlapAndGap(
        { ...updatedShip, loa, berthName },
        otherShips,
        left,
        width,
        top,
        height,
        startDateMs
      );
      if (overlap) {
        setToast({ message: `Li: Tu ${updatedShip.name} b chng ln vi tu ${overlapShip?.name || ''} ti bn ${berthName}. Vui lng kim tra li thi gian v v tr!`, type: 'error' });
        return prev; // Khng cp nht
      }
      
      // Tnh startPosition v endPosition
      let startPosition, endPosition;
      if (updatedShip.start !== undefined && updatedShip.end !== undefined) {
        startPosition = Number(updatedShip.start);
        endPosition = Number(updatedShip.end);
      } else if (left && loa) {
        const leftMatch = left.match(/calc\((-?\d+)\s*\/\s*(\d+)\s*\*\s*100%\)/);
        if (leftMatch) {
          const absStart = parseInt(leftMatch[1]);
          const refStart = berthRef[berthName] || 10;
          startPosition = absStart - refStart;
          endPosition = startPosition + loa;
        }
      }
      
      const updatedList = prev.map(s => s.id === updatedShip.id ? {
        ...s,
        ...updatedShip,
        eta,
        etd,
        berthName,
        loa,
        startPosition,
        endPosition,
        style: {
          ...s.style,
          left,
          width,
          top,
          height
        },
        gapWarning: gapWarning
      } : s);
      setToast({ message: gapWarning
        ? `Cnh bo: Khong cch gia tu ${updatedShip.name} v tu khc nh hn 10% LOA tu ln hn! (Vn cho php cp nht)`
        : ` cp nht k hoch cho tu ${updatedShip.name}.`,
        type: gapWarning ? 'warning' : 'success' });
      setSelectedShip(null);
      return updatedList;
    });
  };

  return (
    <div className="app" onClick={handleAppClick}>
      {/* Password Modal */}
      {showPasswordModal && (
        <>
          <div
            style={{
              position: "fixed",
              top: 0,
              left: 0,
              width: "100vw",
              height: "100vh",
              zIndex: 9998,
              backdropFilter: "blur(6px)",
              WebkitBackdropFilter: "blur(6px)",
              background: "rgba(0,0,0,0.18)"
            }}
          />
          <div style={{
            position: "fixed",
            top: 0,
            left: 0,
            width: "100vw",
            height: "100vh",
            background: "rgba(0,0,0,0.25)",
            zIndex: 9999,
            display: "flex",
            alignItems: "center",
            justifyContent: "center"
          }}>
            <div style={{
              background: "#fff",
              padding: "32px 24px 24px 24px",
              borderRadius: "12px",
              boxShadow: "0 2px 16px rgba(0,0,0,0.15)",
              minWidth: "320px",
              textAlign: "center"
            }}>
              <h2 style={{marginBottom: "18px"}}>Nhp mt khu  s dng</h2>
              <input
                type="password"
                value={passwordInput}
                onChange={e => {
                  setPasswordInput(e.target.value);
                  setPasswordError("");
                }}
                style={{
                  fontSize: "18px",
                  padding: "8px 12px",
                  borderRadius: "6px",
                  border: "1px solid #ccc",
                  width: "100%",
                  marginBottom: "12px"
                }}
                placeholder="Mt khu..."
                autoFocus
                onKeyDown={e => {
                  if (e.key === "Enter") {
                    if (passwordInput === getCurrentPassword()) {
                      setShowPasswordModal(false);
                      setCurrentPassword(passwordInput);
                    } else {
                      setPasswordError("Mt khu khng ng!");
                    }
                  }
                }}
              />
              <button
                style={{
                  fontSize: "16px",
                  padding: "8px 24px",
                  borderRadius: "6px",
                  background: "#1976d2",
                  color: "#fff",
                  border: "none",
                  cursor: "pointer"
                }}
                onClick={() => {
                  if (passwordInput === getCurrentPassword()) {
                    setShowPasswordModal(false);
                    setCurrentPassword(passwordInput);
                  } else {
                    setPasswordError("Mt khu khng ng!");
                  }
                }}
              >Vo chng trnh</button>
              {passwordError && (
                <div style={{color: "#d32f2f", marginTop: "10px"}}>{passwordError}</div>
              )}
              <div style={{marginTop: "10px", fontStyle: "italic", color: "#555"}}>
                Password hin ti: {getCurrentPassword()}
              </div>
              <div style={{marginTop: "18px", fontSize: "0.95em", color: "#888"}}>
                 Nguyen Hoang &amp; Ban Khai thac | Trung tam DHKT KV TAN THUAN
              </div>
            </div>
          </div>
        </>
      )}
      {!showPasswordModal && (
        <>
          {/* Modal xc nhn xa tu */}
          {confirmRemove.show && (
            <div style={{
              position: "fixed",
              top: 0,
              left: 0,
              width: "100vw",
              height: "100vh",
              background: "rgba(0,0,0,0.25)",
              zIndex: 9999,
              display: "flex",
              alignItems: "center",
              justifyContent: "center"
            }}>
              <div style={{
                background: "#fff",
                padding: "32px 24px 24px 24px",
                borderRadius: "12px",
                boxShadow: "0 2px 16px rgba(0,0,0,0.15)",
                minWidth: "320px",
                textAlign: "center"
              }}>
                <h2 style={{marginBottom: "18px"}}>Xc nhn ri tu</h2>
                <div style={{marginBottom: "18px"}}>
                  Bn c chc chn mun cho tu <b>{confirmRemove.ship?.name}</b> ri cu?
                </div>
                <button
                  style={{fontSize: "16px", padding: "8px 24px", borderRadius: "6px", background: "#d9534f", color: "#fff", border: "none", cursor: "pointer", marginRight: "8px"}}
                  onClick={confirmRemoveShip}
                >Xc nhn</button>
                <button
                  style={{fontSize: "16px", padding: "8px 24px", borderRadius: "6px", background: "#eee", color: "#333", border: "none", cursor: "pointer"}}
                  onClick={cancelRemoveShip}
                >Hy</button>
              </div>
            </div>
          )}

          {/* Modal xc nhn xa tu khi waiting list */}
          {confirmDeleteWaiting.show && (
            <div style={{
              position: "fixed",
              top: 0,
              left: 0,
              width: "100vw",
              height: "100vh",
              background: "rgba(0,0,0,0.25)",
              zIndex: 9999,
              display: "flex",
              alignItems: "center",
              justifyContent: "center"
            }}>
              <div style={{
                background: "#fff",
                padding: "32px 24px 24px 24px",
                borderRadius: "12px",
                boxShadow: "0 2px 16px rgba(0,0,0,0.15)",
                minWidth: "320px",
                textAlign: "center"
              }}>
                <h2 style={{marginBottom: "18px"}}>Xc nhn xa tu</h2>
                <div style={{marginBottom: "18px"}}>
                  Bn c chc chn mun xa tu <b>{confirmDeleteWaiting.ship?.name}</b> khi danh sch ch?
                </div>
                <button
                  style={{fontSize: "16px", padding: "8px 24px", borderRadius: "6px", background: "#d9534f", color: "#fff", border: "none", cursor: "pointer", marginRight: "8px"}}
                  onClick={confirmDeleteWaitingShip}
                >Xc nhn</button>
                <button
                  style={{fontSize: "16px", padding: "8px 24px", borderRadius: "6px", background: "#eee", color: "#333", border: "none", cursor: "pointer"}}
                  onClick={cancelDeleteWaitingShip}
                >Hy</button>
              </div>
            </div>
          )}
          <Toast message={toast.message} type={toast.type} onClose={() => setToast({ message: '', type: 'info' })} />
          <Header 
            numDays={numDays}
            onDayChange={handleDayChange}
            startDate={startDate}
            onStartDateChange={handleStartDateChange}
            onSavePlan={async () => {
              try {
                const dataToSave = {
                  berthedShips,
                  waitingShips,
                  startDate,
                  numDays,
                  cranes: cranePositionsRef.current || []
                };
                const dataStr = JSON.stringify(dataToSave, null, 2);
                const blob = new Blob([dataStr], { type: 'application/json' });
                const url = URL.createObjectURL(blob);
                const link = document.createElement('a');
                link.href = url;
                link.download = `berth-plan-${new Date().toISOString().split('T')[0]}.json`;
                document.body.appendChild(link);
                link.click();
                document.body.removeChild(link);
                URL.revokeObjectURL(url);
                setToast({ message: ' lu k hoch thnh cng!', type: 'success' });
              } catch (err) {
                setToast({ message: 'Li khi lu k hoch!', type: 'error' });
              }
            }}
            onOpenPlan={handleOpenPlan}
            onClearPlan={() => {
              if (window.confirm('Bn c chc mun xa ton b k hoch khng?')) {
                setBerthedShips([]);
                setWaitingShips([]);
                cranePositionsRef.current = [];
                setSelectedShip(null);
                setToast({ message: ' xa k hoch thnh cng!', type: 'info' });
              }
            }}
            onExportPDF={async () => {
              try {
                setToast({ message: 'ang xut PDF...', type: 'info' });
                const plannerElement = document.querySelector('.planner-container');
                if (!plannerElement) {
                  setToast({ message: 'Khng tm thy vng planner!', type: 'error' });
                  return;
                }
                const canvas = await html2canvas(plannerElement, {
                  scale: 2,
                  useCORS: true,
                  logging: false,
                  backgroundColor: '#ffffff'
                });
                const imgData = canvas.toDataURL('image/png');
                const pdf = new jsPDF({
                  orientation: canvas.width > canvas.height ? 'landscape' : 'portrait',
                  unit: 'px',
                  format: [canvas.width, canvas.height]
                });
                pdf.addImage(imgData, 'PNG', 0, 0, canvas.width, canvas.height);
                pdf.save(`berth-plan-${new Date().toISOString().split('T')[0]}.pdf`);
                setToast({ message: ' xut PDF thnh cng!', type: 'success' });
              } catch (err) {
                console.error('Error exporting PDF:', err);
                setToast({ message: 'Li khi xut PDF!', type: 'error' });
              }
            }}
          />
          <div className="main-content">
            <div className="planner-container">
              <BerthPlanner 
                numDays={numDays}
                startDate={startDate}
                berthedShips={berthedShips}
                onShipSelect={handleSelectShip}
                onShipPositionChange={handleShipPositionChange}
                onShipDragEnd={handleShipDragEnd}
                activeBerth={activeBerth}
                selectedShipId={selectedShip?.id}
                onRemoveShip={handleRemoveShip}
                onMoveToWaiting={handleMoveToWaiting}
                setCranePositionsRef={ref => { cranePositionsRef.current = ref; }}
              />
            </div>
            <div className="right-sidebar">
              {currentSelectedShip ? (
                <DetailPanel 
                  key={currentSelectedShip.id}
                  ship={currentSelectedShip} 
                  onClose={handleClosePanel} 
                  onUpdate={handleUpdateShipPlan}
                  onToast={(msg, type) => setToast({ message: msg, type: type || 'info' })}
                />
              ) : (
                <ControlPanel 
                  waitingShips={waitingShips} 
                  onShipSelect={handleSelectShip}
                  onAddWaitingShip={handleAddWaitingShip}
                  onDockShip={handleDockShipFromWaiting}
                  onDeleteWaitingShip={handleDeleteWaitingShip}
                />
              )}
            </div>
          </div>
          {/* Copyright footer */}
          <footer className="app-footer">
             Nguyen Hoang &amp; Ban Khai thac | Trung tam DHKT KV TAN THUAN
          </footer>
        </>
      )}
    </div>
  );
}

export default App;