import React from 'react';
import WaitingShipCard from '../controls/WaitingShipCard';

// File ControlPanel CH cha To tu v Tu ch
function ControlPanel({ waitingShips, onShipSelect, onAddWaitingShip, onDockShip, onDeleteWaitingShip }) { 
  return (
    <div className="panel control-panel">
      {/* === Module To Tu Mi === */}
      <section>
        <h2>To Tu Mi</h2>
        <div className="form-container">
          <div className="form-row">
            <div className="form-group">
              <label>Tn tu</label>
              <input type="text" />
            </div>
            <div className="form-group">
              <label>IMO</label>
              <input type="text" />
            </div>
          </div>
          <div className="form-row">
            <div className="form-group">
              <label>DWT</label>
              <input type="number" />
            </div>
            <div className="form-group">
              <label>LOA (m)</label>
              <input type="number" />
            </div>
            <div className="form-group">
              <label>BEAM (m)</label>
              <input type="number" />
            </div>
          </div>
          <div className="form-row">
            <div className="form-group">
              <label>Loi hng</label>
              <select>
                <option>St thp</option>
                <option>Container</option>
                <option>Hng khc</option>
              </select>
            </div>
            <div className="form-group">
              <label>S lng</label>
              <input type="number" />
            </div>
          </div>
          <button className="btn-primary" onClick={() => {
            // Ly gi tr t form
            const form = document.querySelector('.form-container');
            const name = form.querySelector('input[type="text"]')?.value || '';
            const imo = form.querySelectorAll('input[type="text"]')[1]?.value || '';
            let dwt = Number(form.querySelector('input[type="number"]')?.value);
            if (!dwt) dwt = 1000;
            let loa = Number(form.querySelectorAll('input[type="number"]')[1]?.value);
            let beam = Number(form.querySelectorAll('input[type="number"]')[2]?.value);
            if (!loa) loa = 100;
            if (!beam) beam = 20;
            const cargoType = form.querySelector('select')?.value || '';
            const cargo = Number(form.querySelectorAll('input[type="number"]')[3]?.value) || 0;
            if (!name) return;
            const newShip = {
              id: 'W' + Date.now(),
              name,
              imo,
              dwt,
              loa,
              beam,
              cargoType,
              cargo,
            };
            if (onAddWaitingShip) onAddWaitingShip(newShip);
            // Reset cc input/select trong form
            form.querySelectorAll('input').forEach(input => input.value = '');
            form.querySelectorAll('select').forEach(select => select.selectedIndex = 0);
          }}>Thm vo tu ch</button>
        </div>
      </section>

      {/* === Module Tu Ch Cu (Dng Card) === */}
      <section>
        <h2>Tu ang Ch Cu ({waitingShips.length})</h2>
        <div className="waiting-list-container">
          {[...waitingShips]
            .sort((a, b) => {
              const aEta = a.eta ? new Date(a.eta).getTime() : Infinity;
              const bEta = b.eta ? new Date(b.eta).getTime() : Infinity;
              return aEta - bEta;
            })
            .map((ship, index) => (
              <WaitingShipCard 
                key={ship.id} 
                ship={ship} 
                index={index}
                onShipSelect={onShipSelect}
                onDock={onDockShip}
                onDelete={onDeleteWaitingShip}
              />
            ))}
        </div>
      </section>
    </div>
  );
}

export default ControlPanel;